.App {
  text-align: center;
}

.title {
  background-color: #16232b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(90px + 2vmin);
  color: #daffef;
  text-shadow: 5px 5px #6A6B83;
}

#hide {
  display: inline-block;
  max-width: 0%;
  vertical-align: bottom;
  overflow: hidden;
  white-space: nowrap;
  transition: max-width 1s ease-in-out;
}

p {
  cursor: default;
  font-size: 5vw;
}

/* On hover of the h1 tag, we are gonna max out the max-width. */
p:hover #hide {
  max-width: 100%;
}
